import { Component, h, Prop, Element, Method, Host } from '@stencil/core';
import { common } from '../../global/data/common';


@Component({
    tag: 'ks-input-dynamic',
    styleUrl: 'inputDynamic.css'
})
export class InputDynamic {
    @Element() root: HTMLElement;

    @Prop() api: string = "";
    @Prop() message: string = "";
    @Prop({mutable: true, reflect: true}) loading: boolean = false;

    ready: Promise<boolean>;

    componentDidLoad() { 
        const input = this.root.querySelector("input") as HTMLInputElement;
        const component = this.root.querySelector("ks-input-text") as HTMLKsInputTextElement;
        component.setAttribute("novalidate", "novalidate");

        input.onchange = ()=>this.Validate();
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");

        const input = this.root.querySelector("input") as HTMLInputElement;
        const component = this.root.querySelector("ks-input-text") as HTMLKsInputTextElement;

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        this.ready = new Promise<boolean>(async (resolve)=>{
            await component.Validate()
            if(await component.IsValid()) {
                this.loading = true;

                await fetch(this.api + input.value, {
                    method: 'GET', 
                    headers: headers,
                    credentials: "same-origin"
                })
                    .then(response => {
                        if(!response.ok)
                            throw response.status;

                        return response.text();
                    })
                    .then(data => {
                        if (data == "false")
                            throw this.message;

                        else if (data != "true")
                            throw data;

                        else resolve(true);
                    })
                    .catch(error => {
                        let message = error;

                        if(Number.isInteger(error))
                            message = strings.inputConnectionError + error;

                        else if(!navigator.onLine)
                            message = strings.inputInternetConnectionError;
                        
                        component.setAttribute("message", message);
                        resolve(false);
                    });
            }
            else resolve(false);

            this.loading = false;
        });
    }

    @Method()
    async IsValid() {
        return await this.ready;
    }

	render() {
		return <Host>
            <div class="wrapper">
                <slot />
            </div>
            {this.loading && <ks-loader dark />}
        </Host>;
    }
}
